import ERROR from './variables';
import getWeather from './dates&weather';

async function getCurrentLocation() {
  const url = 'https://ipinfo.io/json?token=15472cf1d35d30';
  try {
    const res = await fetch(url);
    const data = await res.json();
    getWeather(data.city);
  } catch (err) {
    ERROR.innerHTML = `Mistake happened: ${err}`;
  }
}
export { getCurrentLocation as default };
