import { BACKGROUND_IMAGE, ERROR } from './variables';

function setBackgroundImage(data4) {
  BACKGROUND_IMAGE.style.background = `url("${data4.urls.regular}") no-repeat`;
  BACKGROUND_IMAGE.style.backgroundSize = 'cover';
}

function findSeason() {
  let season;
  const date = new Date();
  const months = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];
  const month = months[date.getMonth()];
  if (month === 'January' || month === 'February' || month === 'December') {
    season = 'winter';
  } else if (month === 'March' || month === 'April' || month === 'May') {
    season = 'spring';
  } else if (month === 'June' || month === 'July' || month === 'August') {
    season = 'summer';
  } else {
    season = 'autumn';
  }
  return season;
}

function findTimes() {
  let dayTimes;
  const date = new Date();
  const hours = date.getHours();
  if (hours > 5 && hours < 10) {
    dayTimes = 'morning';
  } else if (hours >= 10 && hours < 14) {
    dayTimes = 'noon';
  } else if (hours >= 14 && hours < 18) {
    dayTimes = 'afternoon';
  } else if (hours >= 18 && hours < 22) {
    dayTimes = 'evening';
  } else {
    dayTimes = 'night';
  }
  return dayTimes;
}

async function getLinkToImage() {
  const url = `https://api.unsplash.com/photos/random?orientation=landscape&per_page=1&query=${findSeason()} ${findTimes()}&client_id=wwgykXMhMtDAO9OJi1eEgnNIgjuRuKvmLH1hKvTvilM`;
  try {
    const res = await fetch(url);
    const data4 = await res.json();
    setBackgroundImage(data4);
    // eslint-disable-next-line no-console
    console.log(url);
  } catch (err) {
    ERROR.innerText = ' Unsplash Images Limit Exceeded! Try to test in 1 hour! ';
  }
}

function toggleImages() {
  ERROR.innerText = '';
  getLinkToImage();
}

function smoothImageChange() {
  BACKGROUND_IMAGE.classList.remove('loading');
  BACKGROUND_IMAGE.classList.add('loaded');
}
export { getLinkToImage, toggleImages, smoothImageChange };
