import 'bootstrap/dist/css/bootstrap.min.css';
import './style.css';
import {
  ERROR, SEARCH, BACKGROUND_TOGGLER, GO,
} from './variables';
import { getLinkToImage, toggleImages, smoothImageChange } from './background-image';
import getWeather from './dates&weather';
import getCurrentLocation from './ip';
import displayScale from './temperature-scale';

BACKGROUND_TOGGLER.addEventListener('click', toggleImages);

window.addEventListener('load', () => {
  getCurrentLocation();
  smoothImageChange();
  displayScale();
  getLinkToImage();
});

GO.addEventListener('click', (event) => {
  event.preventDefault();
  ERROR.innerText = '';
  const location = SEARCH.value;
  getLinkToImage();
  getWeather(location);
});
