import { TEMP_FAHRENHEIT } from './variables';

function calcAverageTemp(data2, day) {
  const arr = [];
  let averageTemp;
  for (let i = 0; i < data2.list.length; i += 1) {
    if (day.getDate() === new Date(data2.list[i].dt_txt.toString()).getDate()) {
      arr.push(data2.list[i].main.temp);
    }
  }
  averageTemp = Math.round(arr.reduce((a, b) => (a + b)) / arr.length);
  if (TEMP_FAHRENHEIT.classList.contains('active')) {
    averageTemp = Math.round(averageTemp * (9 / 5) + 32);
  }
  return averageTemp;
}

export { calcAverageTemp as default };
