import {
  TEMP_FAHRENHEIT, DATE_TODAY, DATE_TOMORROW, DATE_AFTER_TOMORROW, DATE_AFTER_AFTER_TOMORROW,
  TEMP_TOMORROW, TEMP_AFTER_TOMORROW, TEMP_AFTER_AFTER_TOMORROW, TEMP_ICON_TOMORROW,
  TEMP_ICON_AFTER_TOMORROW, TEMP_ICON_AFTER_AFTER_TOMORROW, TEMP_CELSIUS, ERROR,
} from './variables';
import calcAverageTemp from './calculate-average-temperature';
import addTodayWeather from './weather-today';
import addLocation from './location';
import setGeolocation from './map';

let timezone;

// Date and time Today
function getCurrentDate() {
  const newDate = new Date();
  const date = new Date(newDate.getTime() + newDate.getTimezoneOffset() * 60000 + timezone * 1000);
  const days = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];
  const dayWeek = days[date.getDay()];
  const day = date.getDate();
  const months = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];
  const month = months[date.getMonth()];
  const hours = date.getHours();
  let minutes = date.getMinutes();
  let seconds = date.getSeconds();
  minutes = (minutes > 9) ? minutes : `0${minutes}`;
  seconds = (seconds > 9) ? seconds : `0${seconds}`;
  DATE_TODAY.innerHTML = `${dayWeek} ${day} ${month} ${hours}:${minutes}:${seconds}`;
  setTimeout(getCurrentDate, 1000);
}

// Future days calculation
function makeDate(daysUntilDate) {
  const newDate = new Date();
  const date = new Date(newDate.getTime() + newDate.getTimezoneOffset() * 60000 + timezone * 1000);
  date.setTime(date.getTime() + daysUntilDate * (1000 * 60 * 60 * 24));
  return date;
}

// Future days setting
function setFutureDays(data2) {
  DATE_TOMORROW.innerHTML = `${makeDate(1, data2).toLocaleString('en', { weekday: 'long' })}`;
  DATE_AFTER_TOMORROW.innerHTML = `${makeDate(2, data2).toLocaleString('en', { weekday: 'long' })}`;
  DATE_AFTER_AFTER_TOMORROW.innerHTML = `${makeDate(3, data2).toLocaleString('en', { weekday: 'long' })}`;
}

// 3-days Weather
function addFutureTemp(data2, date, day, icon) {
  const dateTemp = date;
  dateTemp.innerHTML = `${calcAverageTemp(data2, makeDate(day, data2))}°`;
  const iconImage = icon;
  const array = [];
  for (let i = 0; i < data2.list.length; i += 1) {
    if (makeDate(day).getDate() === new Date(data2.list[i].dt_txt.toString()).getDate()) {
      array.push(data2.list[i]);
    }
  }
  iconImage.src = `http://openweathermap.org/img/wn/${array[4].weather[0].icon}@2x.png`;
}

// Joining many functions into one
function callFunc(data2) {
  addTodayWeather(data2);
  addFutureTemp(data2, TEMP_TOMORROW, 1, TEMP_ICON_TOMORROW);
  addFutureTemp(data2, TEMP_AFTER_TOMORROW, 2, TEMP_ICON_AFTER_TOMORROW);
  addFutureTemp(data2, TEMP_AFTER_AFTER_TOMORROW, 3, TEMP_ICON_AFTER_AFTER_TOMORROW);
}

// Getting all info from openweathermap
async function getWeather(location) {
  const url = `https://api.openweathermap.org/data/2.5/forecast?q=${location}&lang=ua&units=metric&APPID=0f68e761a2c1d64fa537dfe713542064`;
  try {
    const res = await fetch(url);
    const data2 = await res.json();
    timezone = data2.city.timezone;
    // Temperature toggling
    TEMP_FAHRENHEIT.addEventListener('click', (event) => {
      event.preventDefault();
      localStorage.removeItem('celsiusIsActive');
      TEMP_FAHRENHEIT.classList.add('active');
      TEMP_CELSIUS.classList.remove('active');
      localStorage.setItem('fahrenheitIsActive', 'true');
      callFunc(data2);
    });

    TEMP_CELSIUS.addEventListener('click', (event) => {
      event.preventDefault();
      localStorage.removeItem('fahrenheitIsActive');
      TEMP_FAHRENHEIT.classList.remove('active');
      TEMP_CELSIUS.classList.add('active');
      localStorage.setItem('celsiusIsActive', 'true');
      callFunc(data2);
    });
    addLocation(data2);
    getCurrentDate();
    callFunc(data2);
    setFutureDays(data2);
    setGeolocation(data2);
    document.body.classList.add('loaded_hiding');
    window.setTimeout(() => {
      document.body.classList.add('loaded');
      document.body.classList.remove('loaded_hiding');
    }, 500);
  } catch (err) {
    ERROR.innerText = `Mistake happened: ${err}`;
  }
}

export { getWeather as default };
