import { LATITUDE, LONGITUDE } from './variables';

function setMap(lat, lng) {
  // eslint-disable-next-line no-undef
  mapboxgl.accessToken = 'pk.eyJ1IjoidmljdG9yaWEtayIsImEiOiJja2F5MW43N2MwOWtvMnFwaXI4MGFucjdlIn0.xA1XcSZwJwxJ_2C7IcDZGA';
  // eslint-disable-next-line no-undef
  const map = new mapboxgl.Map({
    container: 'map',
    style: 'mapbox://styles/mapbox/streets-v11',
    center: [lng, lat],
    zoom: 8,
  });

  // eslint-disable-next-line no-undef
  const marker = new mapboxgl.Marker();
  marker.setLngLat([lng, lat]);
  marker.addTo(map);
}

function setGeolocation(data2) {
  const lat = +data2.city.coord.lat;
  const lng = +data2.city.coord.lon;
  LATITUDE.innerHTML = `Latitude: ${Math.floor(lat)}°${Math.round((lat - Math.floor(lat)) * 60)}'`;
  LONGITUDE.innerHTML = `Longitude: ${Math.floor(lng)}°${Math.round((lng - Math.floor(lng)) * 60)}'`;
  setMap(lat, lng);
}
export { setGeolocation as default };
