import {
  TEMP_FAHRENHEIT, TEMP_TODAY, FEELS_LIKE, TEMP_ICON, WIND_SPEED, HUMIDITY,
} from './variables';

// Today Weather
function addTodayWeather(data2) {
  if (TEMP_FAHRENHEIT.classList.contains('active')) {
    TEMP_TODAY.innerHTML = `${Math.round(data2.list[0].main.temp * (9 / 5) + 32)}°`;
    FEELS_LIKE.innerHTML = `Feels Like: ${Math.round(data2.list[0].main.feels_like * (9 / 5) + 32)}°`;
  } else {
    TEMP_TODAY.innerHTML = `${Math.round(data2.list[0].main.temp)}°`;
    FEELS_LIKE.innerHTML = `Feels Like: ${Math.round(data2.list[0].main.feels_like)}°`;
  }
  TEMP_ICON.src = `http://openweathermap.org/img/wn/${data2.list[0].weather[0].icon}@2x.png`;
  WIND_SPEED.innerHTML = `Wind Speed: ${Math.round(data2.list[0].wind.speed)}m/s`;
  HUMIDITY.innerHTML = `Humidity: ${data2.list[0].main.humidity}%`;
}
export { addTodayWeather as default };
