import { TEMP_FAHRENHEIT, TEMP_CELSIUS } from './variables';

function displayScale() {
  const fahrIsActive = localStorage.getItem('fahrenheitIsActive');
  if (fahrIsActive === 'true') {
    TEMP_FAHRENHEIT.classList.add('active');
    if (TEMP_CELSIUS.classList.contains('active')) {
      TEMP_CELSIUS.classList.remove('active');
    }
  }
  const celsIsActive = localStorage.getItem('celsiusIsActive');
  if (celsIsActive === 'true') {
    TEMP_CELSIUS.classList.add('active');
  }
}

export { displayScale as default };
